import React from "react";
import "./main.css";

const Main = () => {
  return (
    <main class="padding">
      <div class="today">
        <div>
          <span>TODAY'S BUCKET</span>
        </div>
        <div class="today_style">
          <p>  .</p>
        </div>
      </div>
      <div class="size"></div>
      <div class="size"></div>
      <div class="done">
        <div>
          <span>DONE HISTORY</span>
        </div>
        <div class="done_style">
          <p>  .</p>
        </div>
      </div>
    </main>
  );
};

export default Main;
